body {
margin: 0;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
font-size: 1rem;
font-weight: 400;
line-height: 1.5;
}

.card-custom {
position: relative;
display: flex;
flex-direction: column;
min-width: 0;
word-wrap: break-word;
background-color: #fff;
background-clip: border-box;
border: 3px solid rgba(57, 57, 57, 0.13);
border-radius: .25rem;
}

@media screen and (max-width:767px){ 
.services-works .card .img-hover-top {
        display: block !important;
    }
}

.services-works .card .img-hover-top {
        display: none;
        position: absolute;
        top: 0;
        z-index: 99;
    }
.services-works .card:hover .img-hover-top {
        display: inline;
}

.services-works  img{
       height: 120px;
       width: 120px;
       pointer-events: none;
    }




.container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1280px !important;
}

@media screen and (max-width:767px){ 
 button{ display: block; }
}

.logo {
  font-family: "Bungee", sans-serif;
}

.hero-section {
border-color: transparent;
overflow: hidden;
position: relative;
padding: 120px 0px;
}

.services-section { border-color: transparent;
    overflow: hidden;
    position: relative;
    padding: 100px 0px; 
    background-size: contain;
}


.card-hover:hover {cursor: pointer; transform: scale(1.03);}
.card-hover {transition: all .15s ease-in-out;}

.stretched-link:hover {text-decoration: none !important;}


.navbar {
  width: 100%;
  padding: 10px;
  position: fixed;
  top: 0;
  transition: top 0.6s;
}

.navbar--hidden {
  top: -50px;
}



.services-items {padding-top: 60px;}
.start-section {padding: 100px 0px;}

.footer-section {  padding: 70px 0px 100px 0px;  }

.cookie-consent {
background-color: rgba(67,91,113,.9);
padding: 25px 30px 30px;
border-radius: 15px;
position: fixed;
bottom: 30px;
left: 30px;
right: 30px;
transition: all .2s ease-out;
box-shadow: 0 2px 43px -4px rgba(0,0,0,.19);
z-index: 2000;
max-width: 400px;
}
.cookie-consent .message {color:white;}
.cookie-consent .actions {color:white;}


@media screen and (min-width:768px){ 
.cookie-consent {
bottom: 30px;
right: 30px;
left: auto;
}
}
@media screen and (max-width:768px){ 
.hero-section:after {
display:none;

}
}


.border-long-dash {
